import { VGameObject } from "../src/app/base/VGameObject"
export class Product {
    cfgId: number | string
    amount: number
}

export default class GoodsShelf extends VGameObject {
    products: Map<string | number, Product>

    protected onLoad(): void {
        this.products = new Map<string | number, Product>()

        let prod = new Product()
        prod.amount = 10
        prod.cfgId = 1
        this.products.set(prod.cfgId, prod)
    }

    public hasProduct(cfgId, v) {
        let pord = this.products.get(cfgId)
        if (pord) {
            return pord.amount >= v
        }
        return false
    }

    public costProdut(cfgId, v) {
        if (this.hasProduct(cfgId, v)) {
            let prod = this.products.get(cfgId)
            prod.amount -= v
        }
    }
}
